<template>
  <div class="dark2">
    <span class="bg2"></span>
    <v-card id="container" dark max-width="600" class="mx-auto">
      <v-toolbar extended class="mt-1" color="blue-grey" dark>
        <v-toolbar-title class="flex text-center">
          <h2 class="text-center mt-10">Quiz Seconda Stagione</h2>
        </v-toolbar-title>
      </v-toolbar>

      <v-progress-linear color="lime lighten-1" :value="progress"></v-progress-linear>

      <v-list subheader two-line v-for="(element, index) in questions.slice(a,b)" :key="index" v-show="quiz">

      <v-dialog v-model="dialog" width="500">
        <template v-slot:activator="{ on, attrs }">
          <v-layout align-end justify-end>
          <v-btn v-bind="attrs" v-on="on" icon text color="lime lighten-1" :disabled="!next">
            <v-icon>mdi-information</v-icon>
          </v-btn>
          </v-layout>
        </template>
        
        <v-card dark>
          <v-card-title class="headline">
            {{ element.question }}
          </v-card-title>

          <v-card-text class="my-5">
            {{ element.detail }}
          </v-card-text>

          <v-divider></v-divider>

          <v-card-actions>
            <v-layout flat align-center justify-center>
              <v-btn color="primary" text @click="dialog = false">
              Chiudi
              </v-btn>
            </v-layout>
          </v-card-actions>
        </v-card>
      </v-dialog>
      <h1 class="text-center">Domanda {{ b }}/{{ questions.length }}</h1>
      
      <v-list-item class="d-flex justify-center text-center" >{{ element.question }}</v-list-item>
      <v-divider class="mt-10"></v-divider>

      <v-list-item-group color="blue-grey--text">
        <template v-for="(item, index) in element.suggestions">
      <v-list-item id="list" class="d-flex justify-center my-2" :key="index" :class="select ? check(item) :  ''" @click="selectResponse(item)" :disabled="lockSelection">
        {{ item.suggestion }}
        <v-icon class="mx-5" color="white" v-if="select ? item.correct : ''">mdi-check</v-icon>
        <v-icon class="mx-5" color="white" v-if="select ? !item.correct : ''">mdi-close</v-icon>
      </v-list-item>
      <v-divider v-if="index < element.suggestions.length - 1" :key="`${index}-divider`"></v-divider>
      </template>
      </v-list-item-group>
    </v-list>

      <v-divider></v-divider>

      <v-card dark max-width="600" class="mx-auto" v-if="score_show">
        <h2 class="text-center my-4">Il tuo punteggio </h2>
        <h2 class="text-center my-4">{{ score }}/{{ questions.length }}</h2>
        <v-card-actions class="justify-center">
          <v-btn class="mx-10" @click="restartQuiz">Ricomincia <v-icon right>mdi-reload</v-icon></v-btn>
          <v-btn class="mx-10" @click="$router.push('/')">Seleziona <v-icon right>mdi-home</v-icon></v-btn>
        </v-card-actions>
          <v-divider class="mt-5"></v-divider>
      </v-card>

      <v-card-actions class="justify-center">
        <v-btn class="mx-10" color="blue-grey darken-1" @click="skipQuestion">
          Salta
        </v-btn>
        <v-btn :disabled="!next" class="mx-10" color="blue-grey darken-1" @click="nextQuestion">
          Avanti
        </v-btn>
      </v-card-actions>
    </v-card>
  </div>
</template>

<script>
export default {
  data(){
    return{
      questions: [
        {
          detail:"Allľinizio della stagione, due persone lavorano al tunnel nel 1921: Noah ragazzo e quello che probabilmente è Bartosz adulto. Dopo un discorso tra i due, il primo ucciderà il secondo con un piccone.",
          question:'Quante persone stanno lavorando nelle grotte per scavare i tunnel, il 21 giugno 1921?',
          suggestions: [
            {suggestion:'1'},
            {suggestion:'3'},
            {suggestion:'2', correct: true},
            {suggestion:'Nessuna'},
          ]
        },
        {
          detail:"Ad affiancare Charlotte nelle indagini sulle persone scomparse, ci sarà il commissario Clausen, persona enigmatica e in cerca di risposte sulla scomparsa del fratello, avvenuta 34 anni prima.",
          question:"Come si chiama il detective che affiancherá la polizia di Winden per le indagini sulle persone scompare, fin dall'inizio della seconda stagione?",
          suggestions: [
            {suggestion:'Torben Wöller'},
            {suggestion:'Clausen', correct: true},
            {suggestion:'Jankowski'},
            {suggestion:'Nielsen'},
          ]
        },
        {
          detail:"Nel 2053, vediamo un gruppo di sopravvissuti all'apocalisse capitanati da Elisabeth adulta, che esorta tutti a stare lontani dalla 'zona morta', impiccando chi trasgredisce le regole.",
          question:"Chi è il leader dei sopravvissuti nella Winden del 2053?",
          suggestions: [
            {suggestion:'Claudia'},
            {suggestion:'Elisabeth', correct: true},
            {suggestion:'Silja'},
            {suggestion:'Agnes'},
          ]
        },
        {
          detail:"Claudia Tiedemann riceve una strana visita alla centrale nucleare da parte di una donna molto anziana. Questa non è altro che lei stessa più vecchia, che le spiega l'esistenza dei viaggi nel tempo.",
          question:"Chi rivela a Claudia adulta dell'esistenza dei viaggi nel tempo, nel 1987?",
          suggestions: [
            {suggestion:'Noah'},
            {suggestion:'Claudia anziana', correct: true},
            {suggestion:'Jonas'},
            {suggestion:'Helge'},
          ]
        },
        {
          detail:"Lo Straniero porta Hannah nel 1987 per dimostrarle dell'esistenza dei viaggi nel tempo. Qui Hannah vedrà Mikkel bambino e comprenderà I'incredibile paradosso di colui che diventerà suo marito.",
          question:"In quale epoca Lo Straniero porta Hannah nel futuro grazie al dispositivo di Tannhaus?",
          suggestions: [
            {suggestion:'Nel 1954'},
            {suggestion:'Nel 1986'},
            {suggestion:'Nel 1987', correct: true},
            {suggestion:'Nel 1921'},
          ]
        },
        {
          detail:"Dopo che Claudia anziana spiega tutto quello che sa a sé stessa adulta, torna nel 1954 dove sotterrerà la valigetta in modo che la sua versione più giovane la possa trovare nel 1987.",
          question:"Come ottiene Claudia il dispositivo di Tannhaus per viaggiare nel tempo?",
          suggestions: [
            {suggestion:'Glielo consegna Lo Straniero'},
            {suggestion:'Glielo consegna Noah'},
            {suggestion:'Lo trova seppellito nel giardino di casa', correct: true},
            {suggestion:'Gielo consegna Claudia anziana'},
          ]
        },
        {
          detail:"Helge bambino torna nel 1954 (dopo 7 mesi) grazie alla sedia nel bunker, adesso funzionante. Da questo momento inizierà quel rapporto tra Noah ed Helge che li porterà a collaborare nei rapimenti.",
          question:"Come fa Helge bambino a tornare nel 1954?",
          suggestions: [
            {suggestion:'Con la sedia nel bunker', correct: true},
            {suggestion:'Viaggiando attraverso le grotte'},
            {suggestion:'Con il dispositivo di Tannhaus'},
            {suggestion:'Con la materia oscura'},
          ]
        },
        {
          detail:"Agnes svela a Noah che le ultime pagine del diario sono nelle mani di Claudia anziana; ma, in cambio, Noah dovrà convincere Adam a riammettere Agnes nel Sic Mundus prima del'inizio dell'ultimo ciclo.",
          question:"Chi rivela a Noah dove si trovano le pagine mancanti del libro Sic Mundis?",
          suggestions: [
            {suggestion:'Noah giovane'},
            {suggestion:'Claudia'},
            {suggestion:'Agnes', correct: true},
            {suggestion:'Adam'},
          ]
        },
        {
          detail:"Claudia adulta va nel 2020, qui si informerà sui vari eventi che accadranno a Winden nel futuro. Scoprirà cosi quando sarebbe morto suo padre e vedrà di persona sua figlia Regina con il cancro.",
          question:"In quale epoca arriva Claudia Tiedemann nel suo primo viaggio nel tempo, grazie al dispositivo di Tannhaus?",
          suggestions: [
            {suggestion:'2019'},
            {suggestion:'2020', correct: true},
            {suggestion:'1954'},
            {suggestion:'1986'},
          ]
        },
        {
          detail:"Nel 2053, Jonas, entrando nella materia oscura stabilizzata, viaggia fino al 1921. Arrivato incontrerà Erna che lo ospiterà, inoltre, incontrerà un ragazzo che dimostrerà di sapere chi sia.",
          question:"In quale epoca arriva Jonas dopo aver viaggiato attravero la materia oscura del 2053?",
          suggestions: [
            {suggestion:'1920'},
            {suggestion:'1921', correct: true},
            {suggestion:'1986'},
            {suggestion:'2020'},
          ]
        },
        {
          detail:"Nel 2053, mentre Jonas cerca di stabilizzare la materia oscura, arriva Silja, che in seguito, dopo che Jonas attraversa la materia oscura stabilizzata, incontra Elisabeth e chiede spiegazioni su di essa.",
          question:"Chi è presente con Jonas nella centrale nucleare quando entra nella materia oscura, nel 2053?",
          suggestions: [
            {suggestion:'Silja', correct: true},
            {suggestion:'Elisabeth adulta'},
            {suggestion:'Entrambe'},
            {suggestion:'Nessuno'},
          ]
        },
        {
          detail:"Nel bunker, Charlotte, Peter, The Stranger e Hannah discutono di ciò che è successo. Charlotte si offre di rivelare a Katharina dove si trovano Mikkel e Ulrich, ma Katharina non crederà a una parola.",
          question:"Chi rivela a Katharina che Mikkel e Ulrich sono vivi ma in epoche diverse?",
          suggestions: [
            {suggestion:'Charlotte', correct: true},
            {suggestion:'Peter'},
            {suggestion:'Hannah'},
            {suggestion:'Lo Straniero'},
          ]
        },
        {
          detail:"Il ragazzo misterioso accompagna Jonas alla chiesa, dove incontra Noah che rivela che quel ragazzo è sé stesso giovane. Quindi lo accompagna da Adam, il quale gli rivelerà di essere lui ma anziano.",
          question:"Chi incontra Jonas dopo essere stato condotto nella sede di Sic Mundus?" ,
          suggestions: [
            {suggestion:'Adam', correct: true},
            {suggestion:'Magnus'},
            {suggestion:'Agnes'},
            {suggestion:'Franziska'},
          ]
        },
        {
          detail:"Ulrich, durante una visita di Egon, scopre che suo figlio Mikkel si trova nel suo stesso anno; attua quindi un piano per fuggire e cercarlo. Ma viene fermato dalla polizia che lo riporta in istituto.",
          question:"Perché Ulrich Nielsen fugge dall'ospedale psichiatrico nel quale è rinchiuso nel 1987?",
          suggestions: [
            {suggestion:'Per uccidere Egon'},
            {suggestion:'Per incontrare la madre'},
            {suggestion:'Per cercare Mikkel', correct: true},
            {suggestion:'Nessuna di queste'},
          ]
        },
         {
          detail:"Dopo aver convinto Bartosz a mostrare loro come usare la macchina del tempo, Martha e gli altri viaggiano fino al 1987 dove vedono Ulrich che, nella macchina della polizia, viene riportato in istituto.",
          question:"Dopo aver viaggiato nel tempo per la prima volta, in quale epoca si ritrovano Martha e gli altri ragazzi?",
          suggestions: [
            {suggestion:'1986'},
            {suggestion:'2019'},
            {suggestion:'1987', correct: true},
            {suggestion:'1954'},
          ]
        },
         {
          detail:"Mentre Charlotte cerca indizi nel negozio di nonno, riceve la visita di Noah, che sembra conoscerla molto bene. L'uomo le rivela parecchie cose, tra queste, il fatto di essere suo padre.",
          question:"Cosa scopre Charlotte parlando con Noah, all'interno del vecchio negozio di Tannhaus?",
          suggestions: [
            {suggestion:"L'identitá della madre"},
            {suggestion:"L'identitá del padre", correct: true},
            {suggestion:'Entrambe le cose'},
            {suggestion:'Nessuna di queste'},
          ]
        },
         {
          detail:"Dopo la conversazione con Adam, quest'ultimo convince Jonas di poter cambiare gli eventi tornando dove tutto è iniziato. Jonas, quindi, andrà al 20 giugno 2019, il giorno prima del suicidio del padre.",
          question:"In quale giorno e in che anno arriva Jonas dopo aver viaggiato con la materia oscura nella sede di Sic Mundus?",
          suggestions: [
            {suggestion:"20 Giugno 2020"},
            {suggestion:"21 Giugno 2019"},
            {suggestion:'20 Giugno 2019', correct: true},
            {suggestion:'Nessuna di queste'},
          ]
        },
        {
          detail:"I ragazzi trovano il medaglione di San Cristoforo, il protettore dei viaggiatori. Lo stesso medaglione che Lo Straniero lascerà in camera di Martha, facendole capire che lui è tornato nella sua epoca.",
          question:"Cosa trovano Martha e Jonas nella sabbia quando si trovano al lago?",
          suggestions: [
            {suggestion:"Il medaglione di Sant'Anna"},
            {suggestion:"Il medaglione di San Cristoforo", correct: true},
            {suggestion:'Il medaglione di San Lorenzo'},
            {suggestion:'Il medaglione di San Claudio'},
          ]
        },
        {
          detail:"Mentre Jonas cerca di impedire a Michael di suicidarsi, arriva Claudia che sembra conoscere entrambi. Ella convince Jonas a lasciare il padre al proprio destino, perché tutto vada come è sempre andato.",
          question:"Chi entra in casa Kahnwald mentre Jonas cerca di impedire al padre di impiccarsi?",
          suggestions: [
            {suggestion:"Adam"},
            {suggestion:"Noah"},
            {suggestion:'Claudia', correct: true},
            {suggestion:'Hannah'},
          ]
        },
        {
          detail:"Hannah usa la valigetta per andare nel 1954. Qui, dopo aver visto UIrich e averlo lasciato al suo destino, si presenta a Egon come 'Katharina Nielsen'; si presume che i due avranno una relazione.",
          question:"In quale epoca arriva Hannah nel suo secondo viaggio nel tempo?",
          suggestions: [
            {suggestion:"1954", correct: true},
            {suggestion:"1953"},
            {suggestion:'1986'},
            {suggestion:'1987'},
          ]
        },
        {
          detail:"Clausen scopre che Aleksander rubò l'identità del fratello scomparso 34 anni prima (Aleksander Köhler), cosi lo arresta; difatti Aleksander si troverà in prigione nel momento dell'apocalisse.",
          question:"Per quale motivo il detective Clausen arresta Aleksander Tiedemann?",
          suggestions: [
            {suggestion:"Per l'omicidio del fratello"},
            {suggestion:"Per una rapina"},
            {suggestion:'Per frode bancaria'},
            {suggestion:"Per furto d'identitá", correct: true},
          ]
        },
        {
          detail:"Dopo che Claudia uccide involontariamente il padre, torna a casa dove arriverà Jonas dicendo che la sé stessa anziana gli ha detto cosa ha appena fatto. Cosi la convincerà a seguirlo nel futuro.",
          question:"Chi entra in casa di Claudia Tiedemann nel 1987 per portarla nel 2020?",
          suggestions: [
            {suggestion:"Claudia anziana"},
            {suggestion:"Jonas", correct: true},
            {suggestion:'Helge'},
            {suggestion:"Agnes"},
          ]
        },
         {
           detail:"Poco prima che scoppi l'apocalisse, arrivano nel bunker Peter ed Elisabeth, arrivano poi Claudia adulta e Regina. Per ultimo, arriva Noah ragazzo (dal 1921) che incontrerà Elisabeth per la prima volta.",
          question:"Quante persone sono presenti nel bunker qualche istante prima che scoppi l'apocalisse?",
          suggestions: [
            {suggestion:"4"},
            {suggestion:"5", correct: true},
            {suggestion:'3'},
            {suggestion:"6"},
          ]
        },
        {
          detail:"Prima di andare nel bunker, Noah ragazzo dà a The Stranger una lettera da parte di Martha. contenuto sconvolgerà l'uomo, che si precipiterà a salvare i ragazzi, portandoli via con la valigetta.",
          question:"Quali personaggi sono stati salvati dallo Straniero poco prima dell'apocalisse?",
          suggestions: [
            {suggestion:"Magnus, Martha e Franziska"},
            {suggestion:"Elisabeth, Franziska e Bartosz"},
            {suggestion:'Martha, Magnus ed Elisabeth'},
            {suggestion:"Bartosz, Magnus e Franziska", correct: true},
          ]
        },
        {
          detail:"Adam uccide Martha, così che questo dolore porti Jonas a diventare lui. Lo stesso Jonas verrà poi salvato dall'apocalisse da una Martha proveniente da un altro mondo, che lo porterà con sé.",
          question:"Chi muore qualche istante prima che l'apocalisse colpisca Winden?",
          suggestions: [
            {suggestion:"Elisabeth adulta"},
            {suggestion:"Hannah"},
            {suggestion:'Martha', correct: true},
            {suggestion:"Charlotte"},
          ]
        },
      ],
      a: 0,
      b: 1,
      select: false,
      score: 0,
      quiz: true,
      score_show: false,
      next: false,
      progress: 0,
      lockSelection: false,
      dialog: false,
    }
  },
  
  methods:{
    selectResponse(e){
      this.select = true;
      this.next = true;
      this.lockSelection = true;

      if(e.correct) {
        this.score++;
      }
    },

    check(status){
      if (status.correct) {
        return 'correct'
      }
      else{
        return 'incorrect'
      }
    },

    nextQuestion(){
      this.lockSelection = false;
      if (!this.next) {
        return;
      }

      this.progress = this.progress + (100/this.questions.length);

      if (this.questions.length - 1 == this.a) {
        this.score_show = true;
        this.quiz = false;
      }
      else{
        this.a++;
        this.b++;
        this.select = false;
        this.next = false;
      }
    },

    skipQuestion(){

      if (this.next) {
        return;
      }

      this.progress = this.progress + (100/this.questions.length);

      if (this.questions.length - 1 == this.a) {
        this.score_show = true;
        this.quiz = false;
      }
      else{
        this.a++;
        this.b++;
      }
    },

    restartQuiz(){
      Object.assign(this.$data, this.$options.data());
    }
  },
}
</script>

<style>
#list.correct{
  background-color: rgb(74, 219, 74);
}

#list.incorrect{
  background-color: rgb(240, 117, 100);
}

</style>